@import url(https://fonts.googleapis.com/css?family=Raleway&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.home {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh; }
  .home .inner {
    text-align: center; }
  .home .home-logo {
    width: 120px;
    height: 120px;
    display: inline-block;
    margin-bottom: 25px; }
    .home .home-logo img {
      width: 100%;
      height: 100%; }
  .home .welcome-title {
    font-size: 2.5rem;
    font-weight: bold;
    text-transform: capitalize;
    text-align: center; }

@media (max-width: 550px) {
  .home .welcome-title {
    font-size: 2rem; } }

#mainNav {
  padding: 20px 25px; }
  #mainNav .navbar-brand {
    font-weight: bold;
    font-size: 2rem;
    position: relative;
    top: -2px; }
  #mainNav .nav-logo {
    width: 48px;
    height: 48px;
    margin-right: 15px; }
  #mainNav .nav-link {
    font-weight: bold;
    color: #ebebeb;
    font-size: 20px; }
    #mainNav .nav-link:hover {
      color: white; }
  @media (max-width: 450px) {
    #mainNav {
      padding: 20px 10px; }
      #mainNav .navbar-brand {
        font-size: 1.5rem; }
      #mainNav .nav-logo {
        width: 32px;
        height: 32px; } }

.spinner-wrapper {
  height: 500px;
  display: flex;
  justify-content: center;
  align-items: center; }

.country-list {
  margin-top: 25px;
  margin-bottom: 25px; }
  .country-list .rt-th,
  .country-list .rt-td {
    text-align: center;
    align-items: center;
    display: flex;
    justify-content: center; }

.country-section .country-detail-wrapper {
  margin: 10px 0px; }
  .country-section .country-detail-wrapper .inner-wrapper .single-row {
    padding: 10px 0px;
    display: flex;
    align-items: center; }
    .country-section .country-detail-wrapper .inner-wrapper .single-row .content-column {
      font-size: 18px; }
    .country-section .country-detail-wrapper .inner-wrapper .single-row .label-column {
      width: 150px;
      flex-shrink: 0;
      flex-grow: 0;
      font-weight: bold; }
    .country-section .country-detail-wrapper .inner-wrapper .single-row .value-column {
      flex-grow: 1; }
      .country-section .country-detail-wrapper .inner-wrapper .single-row .value-column img {
        width: 2rem !important;
        height: 2rem !important;
        position: relative;
        top: 2px; }

.country-section .footer {
  margin-top: 25px; }

body {
  font-family: 'Raleway', sans-serif; }

.App {
  text-align: center; }

.App-logo {
  height: 40vmin; }

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white; }

.App-link {
  color: #09d3ac; }

.section-container {
  padding: 25px 15px;
  min-height: 100vh; }
  .section-container .page-title-wrapper {
    padding: 15px 0px; }
    .section-container .page-title-wrapper .page-title {
      font-weight: bold;
      font-size: 35px; }

@media (max-width: 550px) {
  .section-container .page-title-wrapper .page-title {
    font-size: 30px; } }

.mobiletest-div {
  display: none; }

